body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
  box-sizing: border-box;
}

body{
  margin: 0;
  background-color: #33373E;
  font-family: Inter, sans-serif;
}

.navbar{
  background-color: #21222A;
  display: flex;
  align-items: center;
  padding: 30px 25px;

}

.navbar > h3{
  color: #61DAFB;
  font-weight: 700;
}

.navbar > img{
  margin-right: 7px;
}

.navbar > h3, .navbar > h4{
  margin: 0;
}

.navbar>h4{
  color: #fff;
  margin: auto;
  font-weight: 600;
}

/* main content */
.main{
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  padding: 57px 27px;
  background-image: url(./images/reactjs-icon\ 2.png);
  background-repeat: no-repeat;
  background-position: right 75%;
}

.main > h1, ul{
  color: #fff;
}

.main > h1{
  letter-spacing: -0.05em;
}

.main-items{
  margin-top: 20px;
  max-width: 400px;
}

.main-items > li{
  padding-block: 10px;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

.main-items > li::marker{
  font-size: 1.2rem;
  color: #61DAFB;
}

